import React, { useState } from 'react'
import '../static/css/pieNav.css'
import PieSlice from './PieSlice'
import PieOverlay from './PieOverlay'
import PieContract from './PieContract'
import { connect } from 'react-redux'
import { setExpanded } from '../reducers/overlayReducer'
import '../static/css/pieNav.css'

const PieNav = (props) => {
    const [pieHovered, setPieHovered] = useState(null)

    const pieHoveredId = (id) => {
        setPieHovered(id)
    }
    let dynamicSliceData = props.slices
    if (pieHovered) {
        const hoveredPiePercent = props.slices.find(s => s.id === pieHovered).percent
        const remainingPercent = 100 - hoveredPiePercent
        if (hoveredPiePercent < 25) {
            dynamicSliceData = props.slices.map(sliceData => {
                if (sliceData.id === pieHovered) {
                    return {...sliceData, percent: 25}
                } else {
                    const newPercent = (((sliceData.percent / remainingPercent) * 100) / 2) * 1.5
                    return {...sliceData, percent: newPercent}
                }
            })
        }
    }

    const dataToDegConverter = (data) => {
        let clock = 0
        return data.map(sliceData => {
            const percToDeg = sliceData.percent * 3.6
            const currentClock = clock
            clock += percToDeg
            return {...sliceData, percent: percToDeg, clock: currentClock}
        })
    }
    dynamicSliceData = dataToDegConverter(dynamicSliceData)
    const slices = () => dynamicSliceData.map(sliceData => {
        return (
            <PieSlice key={sliceData.id} clock={sliceData.clock} toSetPieHovered={pieHoveredId} sliceData={sliceData} />
        )
    })

    const onClickExpand = () => {
        props.setExpanded()
    }
    const pieStyle = 
        props.overlay === 'expanded' ?
        {
            left: '0px',
            top: '0px',
            width: '500px',
            height: '500px',
            transition: 'left 0.5s, top 0.5s, width 0.5s, height 0.5s',
        }
        :
        {
            left: '-50px',
            top: '-50px',
            width: '200px',
            height: '200px',
            transition: 'left 0.5s, top 0.5s, width 0.5s, height 0.5s',
        }
    const currentColor = props.slices.find(s => s.id === props.navTracker) ?
        props.slices.find(s => s.id === props.navTracker).color
        :
        'white'
    return (
        <svg style={pieStyle} viewBox="-10 -10 1020 1020" className="pie-container">
            <PieContract />
            <circle cx="500" cy="500" r="500" fill="white" />
            {slices()}
            <circle cx="500" cy="500" r="40" fill="white" />
            <PieOverlay onClickExpand={onClickExpand} />
        </svg>
    )
}

const mapStateToProps = (state) => {
    return {
        slices: state.slices,
        overlay: state.overlay,
        navTracker: state.navTracker
    }
}

export default connect(
    mapStateToProps,
    { setExpanded }
)(PieNav)